/* Table Styles */
.table-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

table {
  width: 100%;
  max-width: 800px;
  margin: 20px;
  background-color: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-collapse: collapse;
}

th, td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #dddddd;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

/* Add to Database Form Styles */
.AddTable {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.movie-form {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 80%;
  max-width: 400px;
  color: #333333;
}

.form-input {
  padding: 10px;
  border: 1px solid #dddddd;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
}

.submit-button {
  background-color: #007bff;
  color: #ffffff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #0056b3;
}
